import React, { useEffect, useState, useRef } from "react";
import Scanner from "./Scanner";
// import { useNavigate } from "react-router-dom";

const BarScanner = () => {
  const [scanning, setScanning] = useState(false);
  const [results, setResults] = useState([]);
  const pageOpened = useRef(false);
  //   const navigate = useNavigate();

  //   const handleScanToggle = () => {
  //     setScanning(!scanning);
  //   };
  useEffect(() => {
    if (
      results.length > 0 &&
      results[0].codeResult &&
      results[0].codeResult.code
    ) {
      if (!pageOpened.current) {
        const url = `https://surls.in/${encodeURIComponent(
          results[0].codeResult.code
        )}`;
        window.open(url, "barcodeWindow");
        pageOpened.current = true;
      }
    }
  }, [results]);

  const handleDetected = (result) => {
    setResults([result]); // Update results with the new detection
    setScanning(!scanning);
  };

  return (
    <div className="container mt-4">
      {/* Back Button */}
      <div className="d-flex align-items-center mb-3">
        <a href="/" className="btn btn-secondary me-3">
          <i className="bi bi-arrow-left"></i>{" "}
          {/* Bootstrap 5.2 Back Arrow Icon */}
        </a>
        <h5 className="m-0">Barcode Scanner</h5>
      </div>

      {/* Scanner Area */}
      <div
        className="border rounded mb-4"
        style={{ width: "100%", height: "100%" }}
      >
        <Scanner onDetected={handleDetected} />
      </div>

      {/* Scanned Data Display */}
      <textarea
        className="form-control"
        style={{ fontSize: 20, height: 100 }}
        readOnly
        value={results[0] ? results[0].codeResult.code : "No data scanned"}
      />
    </div>
  );
};

export default BarScanner;
