//import logo from './logo.svg';
import React, { useState } from "react";
import "./App.css";
import Navbar from "./components/Navbar";
import TextForm from "./components/TextForm";
import About from "./components/About";
import Alert from "./components/Alert";
import BarcodeScanner from "./components/BarcodeScanner";
import BarScanner from "./components/BarScanner";
// import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

const path = window.location.pathname;
console.log(path);
function App() {
  const [mode, setMode] = useState("light");
  const [alert, setAlert] = useState(null);
  const [active, setActive] = useState("home");

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  };
  // const removeBodyClasses = () => {
  //   document.body.classList.remove('bg-primary')
  //   document.body.classList.remove('bg-success')
  //   document.body.classList.remove('bg-danger')
  //   document.body.classList.remove('bg-warning')
  // }
  const toggleMode = (cls) => {
    // removeBodyClasses()
    // document.body.classList.add('bg-'+cls)
    if (mode === "light") {
      setMode("dark");
      document.body.style.backgroundColor = "#042743";
      showAlert("Dark mode has been enabled", "success");
    }
    if (mode === "dark") {
      setMode("light");
      document.body.style.backgroundColor = "white";
      showAlert("Light mode has been enabled", "success");
    }
  };

  return (
    <>
      {/* <Router>
        <Navbar title="Textutils" mode={mode} toggleMode={toggleMode} />
        <Alert alert={alert} />
        <div className="container my-3">
          <Routes>
            <Route
              exact
              path="/"
              element={
                <TextForm
                  heading="TextUtils - The game with the text"
                  showAlert={showAlert}
                  mode={mode}
                />
              }
            ></Route>
            <Route path="/about" element={<About mode={mode} />}></Route>
            <Route
              path="/bcscanner"
              element={<BarcodeScanner mode={mode} />}
            ></Route>
          </Routes> */}

      <Navbar title="Textutils" mode={mode} toggleMode={toggleMode} />
      <Alert alert={alert} />
      <div id="naved" className="mb-5">
        <nav className="nav nav-pills justify-content-center">
          <li className="nav-item fs-5 me-4" style={{ fontWeight: "bold" }}>
            <a
              className="nav-link text-dark"
              onClick={(e) => {
                e.preventDefault();
                setActive("home");
              }}
              href="/"
            >
              Home
            </a>
            {active === "home" ? (
              <hr
                className="text-dark"
                style={{ marginTop: "-1%", height: "2px" }}
              />
            ) : (
              ""
            )}
          </li>
          <li
            className="nav-item fs-5 ms-4 me-4"
            style={{ fontWeight: "bold" }}
          >
            <a
              className="nav-link text-dark"
              onClick={(e) => {
                e.preventDefault();
                setActive("about");
              }}
              href="/"
            >
              About
            </a>
            {active === "about" ? (
              <hr
                className="text-dark"
                style={{ marginTop: "-1%", height: "2px" }}
              />
            ) : (
              ""
            )}
          </li>
          <li
            className="nav-item fs-5 ms-4 me-4"
            style={{ fontWeight: "bold" }}
          >
            <a
              className="nav-link text-dark"
              onClick={(e) => {
                e.preventDefault();
                setActive("bcscan");
              }}
              href="/"
            >
              Barcode Scanner
            </a>
            {active === "bcscan" ? (
              <hr
                className="text-dark"
                style={{ marginTop: "-1%", height: "2px" }}
              />
            ) : (
              ""
            )}
          </li>
        </nav>
      </div>
      {active === "home" && (
        <TextForm
          heading="TextUtils - The game with the text"
          showAlert={showAlert}
          mode={mode}
        />
      )}
      {active === "about" && <About mode={mode} />}
      {active === "bcscan" && <BarScanner mode={mode} />}
      {/* </div>
      </Router> */}
    </>
  );
}

export default App;
